import userMixin from '@/mixins/user.js'
    export default {
        data() {
            return {
                current: 'overview',
                transPage: 'all',
                slide: null,
            }
        },
        props: ['title'],
        mixins: [userMixin],
        methods: {
            truncate(input) {
                if (input.length > 10) {
                return input.substring(0, 10) + '...';
                }
                return input;
            },
            toggleTransPage(page) {
                this.transPage = page;
            },
            btnselect(val) {
                this.current = val;
            },
            logout() {
                localStorage.removeItem('norduserxtxtxt');
                localStorage.removeItem('nordtokenxtxtxt');
                this.$router.push('/');
            },
            closeslide() {
                this.slide = null
            },
            openslide(val) {
                this.slide = val
            }
        },
        computed: {
            currentroute() {
                return this.$route.name;
            }
        }
    }
